// .our-region {}

.our-region-block {
  position: relative;

  @media (max-width: 800px) {
    flex-direction: column;
  }
}

.our-region-block:first-child {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 55px;
  padding-bottom: 250px;

  @media (max-width: 1650px) {
    padding-bottom: 0;
  }

  @media (max-width: 800px) {
    padding-bottom: 0;
  }

  .our-region-item {
    position: relative;
    width: 49%;
    display: flex;
    flex-direction: column;

    @media (max-width: 800px) {
      width: 100%;
    }

    &__map {
      &--desktop {
        @media (max-width: 500px) {
          display: none;
        }
      }

      &--mobile {
        display: none;

        @media (max-width: 500px) {
          display: block;
        }
      }
    }

    &__tourists {
      &--desktop {
        @media (max-width: 500px) {
          display: none;
        }
      }

      &--mobile {
        display: none;

        @media (max-width: 500px) {
          display: block;
        }
      }
    }
  }

  .our-region-item:first-child {
    @media (max-width: 800px) {
      margin-bottom: 68.6px;
    }
  }

  .our-region-item:nth-child(2) {

    @media (max-width: 500px) {
      margin-bottom: 21.5px;
    }

    img {
      position: relative;
      top: -50px;

      @media (max-width: 1650px) {
        top: 0;
        width: 100%;
        margin: auto;
      }
    }
  }

  .our-region-item:last-child {
    // width: fit-content;
    width: 60vw;
    max-width: 977px;
    position: absolute;
    bottom: 0;
    left: -320px;
    z-index: -1;

    @media (max-width: 1650px) {
      position: static;
    }

    @media (max-width: 800px) {
      position: static;
      width: 100%;
    }

    img {
      width: 100%;

      @media (max-width: 800px) {
        margin: 0 auto;
      }

      @media (max-width: 500px) {
        width: 100vw;
        margin: 0 -20px;
      }
    }
  }

  .our-region-box {
    max-width: 390px;
    display: flex;
    flex-direction: column;

    @media (max-width: 800px) {
      max-width: 100%;
      width: 100%;
    }

    h4 {
      margin-bottom: 21px;
      color: $red-color;
      font-family: "Bebas Neue", cursive;
      font-size: 34px;
      font-weight: 400;
      line-height: 1;
      text-transform: uppercase;
    }

    p {
      margin-bottom: 20px;
      font-size: 20px;
      line-height: 26px;

      @media (max-width: 500px) {
        margin-bottom: 34px;
      }
    }

    a {
      max-width: 224px;
      width: 100%;
      margin-left: auto;
      padding: 4.8px 0 1.2px;
      color: $dark-green-color;
      font-family: "Bebas Neue", cursive;
      font-size: 25px;
      line-height: 30px;
      text-transform: uppercase;
      text-align: center;
      background: url("../../src/images/agenda/dark-green-frame.svg");
      background-repeat: no-repeat;
      background-size: cover;
      transition: 0.3s;

      @media (max-width: 800px) {
        background: none;
      }
    }

    a:hover {
      color: $light-brown-color;
    }
  }

  .our-region-box:not(:last-child) {
    margin-bottom: 101px;

    @media (max-width: 800px) {
      margin-bottom: 99px;
    }
  }
}

.our-region-block:last-child {
  position: relative;
  padding-bottom: 140px;

  @media (max-width: 1650px) {
    padding-bottom: 0;
  }

  .our-region-item:first-child {
    display: flex;

    @media (max-width: 1650px) {
      margin-bottom: 112px;
    }

    @media (max-width: 1250px) {
      flex-direction: column;
    }

    @media (max-width: 370px) {
      margin-bottom: 30px;
    }

    h4 {
      display: block;
      font-family: "Bebas Neue", cursive;
      font-size: 34px;
      font-weight: 400;
      line-height: 1;
      color: $red-color;

      @media (max-width: 1250px) {
        margin-bottom: 65.7px;
      }

      span {
        display: block;
      }
    }

    img {
      position: relative;
      right: 110px;
      bottom: 21px;
      z-index: -1;

      @media (max-width: 1250px) {
        position: static;
      }
    }

    .our-region-item__image {
      &--desktop {
        @media (max-width: 600px) {
          display: none;
        }
      }

      &--mobile {
        display: none;

        @media (max-width: 600px) {
          display: block;
        }
      }
    }
  }

  .our-region-item:last-child {
    position: absolute;
    bottom: 0;
    right: -340px;
    z-index: -2;

    @media (max-width: 1650px) {
      position: static;
      display: flex;
      justify-content: flex-end;
    }

    img {
      @media (max-width: 600px) {
        width: 100%;
      }
    }
  }
}
