.footer-wb {
  padding: 75px 0 80px;
  background: #000;

  @media (max-width: 500px) {
    padding: 87px 0 20px;
  }

  &__wrapper {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
}

.footer-block-wb {
  display: flex;
  align-items: center;
  width: 100%;

  .footer-box-wb {
    height: 150px;

    @media (max-width: 500px) {
      height: 100px;
    }

    img {
      height: 100%;
      width: 100%;
      object-fit: contain;
    }
  }
}

.footer-block-wb:first-child {
  justify-content: space-between;
  flex-wrap: wrap;

  .footer-box-wb:not(:last-child) {
    @media (max-width: 500px) {
      width: 50%;
      display: flex;
    }

    img {
      @media (max-width: 500px) {
        height: auto;
        width: auto;
        margin: 0 auto;
      }
    }
  }

  .footer-box-wb:last-child {
    @media (max-width: 900px) {
      width: 100%;
      display: flex;
    }

    img {
      @media (max-width: 900px) {
        height: auto;
        width: auto;
        margin: 0 auto;
      }
    }
  }
}

.footer-block-wb:last-child {
  margin: 0 auto;
  width: auto;

  @media (max-width: 500px) {
    flex-direction: column;
  }

  .footer-box-wb:not(:last-child) {
    margin-right: 84px;

    @media (max-width: 500px) {
      margin-right: 0;
    }
  }
}
